/* eslint-disable no-mixed-spaces-and-tabs */
<template>
  <div>
    <div class="background">
      <img
        :src="imgSrc"
        width="100%"
        height="100%"
        alt=""
      />
    </div>

    <div class="front">
      <el-card class="card">
        <div
          slot="header"
          class="title"
        >
          <span></span>
        </div>
        <div>
          <el-form
            ref="form"
            :rules="rules"
            :model="form"
            label-width="100px"
            class="form-all1"
          >

            <el-form-item
              label=""
              prop="name"
              class="form-item"
            >
              <el-input
                v-model="form.name"
                placeholder=""
                class="form-input"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="password"
              class="form-item"
            >
              <el-input
                type="password"
                v-model="form.password"
                placeholder=""
                @keyup.enter.native="loginAction('form')"
                show-password
                class="form-input"
              ></el-input>
            </el-form-item>
            <el-form-item
              v-if="showIpInput"
              label="IP"
              prop="ip"
              class="form-item"
            >
              <el-input
                v-model="form.ip"
                placeholder=""
                class="form-input"
                @keyup.enter.native="loginAction('form')"
              ></el-input>
            </el-form-item>
            <el-form-item
              v-if="showIpInputDB"
              label="IP"
              prop="dbIp"
              class="form-item"
            >
              <el-input
                v-model="form.dbIp"
                placeholder=","
                class="form-input"
                @keyup.enter.native="loginAction('form')"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              label-width="0"
              style="text-align:center"
            >
              <el-button
                type="primary"
                @click="loginAction('form')"
                :loading="loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
              ></el-button>
            </el-form-item>

          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { Message } from "element-ui";
import md5 from "js-md5";

export default {
  data() {
    return {
      imgSrc: require("../../public/image/login.jpg"),
      form: {
        name: "admin",
        password: "",
        ip: "",
        dbIp: "",
      },
      showIpInput: false,
      showIpInputDB: false,
      loading: false,
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        password: [{ required: false, message: "", trigger: "blur" }],
        ip: [{ required: true, message: "ip", trigger: "blur" }],
      },
    };
  },
  mounted() {
    document.addEventListener("keydown", this.saveContent);
    if (localStorage.ip) {
      this.form.ip = localStorage.ip;
    } else {
      this.showIpInput = true;
    }

    if (localStorage.dbIp) {
      this.form.dbIp = localStorage.dbIp;
    }
  },
  beforeDestroy() {
    document.removeEventListener("keydown", this.saveContent);
  },
  methods: {
    loginAction(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          localStorage.ip = this.form.ip;
          localStorage.dbIp = this.form.dbIp;

          const oldPas = this.form.password;
          if (this.form.password.length > 0) {
            this.form.password = md5(this.form.password);
          }

          const that = this;
          that.loading = true;
          this.$httpApi
            .login_post(this.form)
            .then(function (response) {
              that.loading = false;
              if (response.data.code == "0") {
                window.sessionStorage.setItem(
                  "token",
                  response.data.Authorization
                );
                 window.sessionStorage.setItem(
                  "name",
                  that.form.name
                );
                var paramsa = {
                  name: that.form.name,
                };

                console.log("" + JSON.stringify(paramsa));

                if (that.$store.state.routes.length <= 0) {
                  that.$store.commit("initRoutes", response.data.level);
                }

                that.$router.replace({ path: "/home", query: paramsa });
              } else {
                Message.error(response.data.message);
              }
            })
            .catch(function () {
              that.loading = false;
            });
          this.form.password = oldPas;
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    saveContent(e) {
      let key = e.keyCode || e.which || e.charCode;
		
      if (key === 113) {
        if (this.showIpInput) {
          this.showIpInput = false;
        } else {
          this.showIpInput = true;
        }
      } else if (key === 115) {
        if (this.showIpInputDB) {
          this.showIpInputDB = false;
        } else {
          this.showIpInputDB = true;
        }
      }
    },
  },
};
</script>

