import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Lock from '../views/Lock.vue'
import store from '../store'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    meta:{
      hidden:true
    }

  },
  {
    path: '/Lock',
    name: 'Lock',
    component: Lock,
    meta:{
      //hidden:true
    }
  },
  {
    id:1,
    path:'/home',
    component:() => import('@/views/Home.vue'),
    name:'home',
    meta:{
      hidden:true,
      title:'',
      icon:'el-icon-s-home',
    }
  },
  {
    path: '/404',
    name:'404',
    component: () => import('@/views/error-page/404'),
    meta:{
      hidden:true
    }
  }
  

]

const createRouter = () => new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
});

const router = createRouter()

export function resetRouter() {
  
  const newRouter = createRouter();
  router.matcher = newRouter.matcher;
  
}

router.beforeEach((to,from,next) =>{

  if(!window.sessionStorage.getItem('token') && to.path != '/')
  {
    next('/');
    console.log(' token')
  }else
  {
    
    if(to.path != '/' )
    {
      //menu
     var nLev=  window.sessionStorage.getItem('nlevel');
      
     store.commit('initRoutes',nLev);
     
     if(to.name==null){
      next({...to})
      }else{
        next()
      }
      return;
    }


    next();
    

    
  }

})

export default router
