<template>
  <div>
    <el-table
      class="printPage printMark"
      highlight-current-row
      border
      :id="table_id"
      :data="table_data&&table_data.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      style="width: 100%"
      max-height="620px"
      @current-change="handleCurrentChange"
      ref="tableRef"
      v-bind="tableAttri"
      @row-dblclick="dbSelected"
    >
      <el-table-column
        v-if="addSerialNum"
        type="index"
        width="50"
        label=""
        align='center'
      ></el-table-column>
      <el-table-column
        v-for="(uiItem) in table_ui_data"
        :key="uiItem.id"
        :width="uiItem.width "
        :sortable="uiItem.sortable"
        style="width: 100%;"
        :align="uiItem.align ? uiItem.align : 'center'"
        v-bind="uiItem"
        v-on="$listeners"
      >

        <template slot-scope="scope">

          <!--template v-if="uiItem.formatter">
            <span v-html="uiItem.formatter(scope.row,scope.column, scope.row[scope.column.property])" />
          </!--template>
          <template v-else-->

          <el-select
            v-model="scope.row[scope.column.property]"
            v-if="(uiItem.uiType == 1) && scope.row.isEdit && !uiItem.disabled"
            :placeholder="uiItem.placeholder"
          >
            <el-option
              v-for="item in uiItem.selectlist"
              :key="item.id"
              :label="item.name"
              :value="item.val"
            >
            </el-option>
          </el-select>
          <el-input
            style="padding:0"
            class="inputNoBorder"
            :autofocus="true"
            v-else-if="scope.row.isEdit&& !uiItem.disabled"
            v-model="scope.row[scope.column.property]"
          ></el-input>
          <span v-else>{{scope.row[scope.column.property]}}</span>
        </template>

        <!--/template-->
      </el-table-column>

    </el-table>
    <div
      class="block"
      style="margin-top:15px;"
    >
      <el-pagination
        @size-change="handleSizeChange_page"
        @current-change="handleCurrentChange_page"
        background
        layout="total,prev, pager, next"
        :hide-on-single-page="true"
        :total="table_data&&table_data.length"
        :page-size="pageSize"
        :current-page="currentPage"
        align='center'
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";

import html2canvas from "html2canvas";
import printJS from "print-js";

export default {
  props: {
    table_ui_data: Array,
    table_data: {
      type: Array,
      default() {
        return [];
      },
    },
    table_cell_align: {
      type: String,
      default: "center",
    },
    tableAttri: {},
    //id
    table_id: {
      type: String,
      default: undefined,
    },
    table_disable_edit: {
      type: Boolean,
      default: true,
    },
    unique_id: {
      type: Array,
      default() {
        return ["id"];
      },
    },
    addSerialNum: {
      type: Boolean,
      default: false,
    },
    pageSize: {
      type: Number,
      default: 5,
    },
  },
  data() {
    return {
      currentTableData: [],
      currentRow: {},
      msgId: "dataChanged",
      clickRowID: "rowClick",
      dbClickRowID: "dbRowClick",
      currentPage: 1,
    };
  },
  watch: {
    table_data: {
      handler(newvale) {
        if (newvale) {
          this.currentPage = 1;
        } else {
          this.currentPage = 0;
        }

        let isEqual = this.dataIsEqual(newvale, this.currentTableData);

        if (isEqual) {
          console.log("(0 )");
          this.$emit(this.msgId, 0);
          return;
        }
        console.log("");
        this.$emit(this.msgId, 1);
      },
      deep: true,
    },
    table_disable_edit(newVal) {
      if (newVal) {
        this.table_data.forEach((item) => {
          item.isEdit = false;
        });
      }
    },
  },
  methods: {
    // 
    handleSizeChange_page(val) {
      console.log(` ${val} `);
      this.currentPage = 1;
      this.pageSize = val;
    },
    // 
    handleCurrentChange_page(val) {
      console.log(`: ${val}`);
      this.currentPage = val;
    },
    unique_idEqual(obj, obj1) {
      for (let index = 0; index < this.unique_id.length; index++) {
        const element = this.unique_id[index];
        if (obj1[element] != obj[element]) {
          return false;
        }
      }
      return true;
    },
    initCurrentData(data) {
      if (data) {
        this.currentTableData = JSON.parse(JSON.stringify(data));
      } else {
        this.currentTableData = undefined;
      }
    },
    confirmOKAction(callBackChangedFunc) {
      //
      if (this.currentTableData == undefined) {
        this.currentTableData = new Array();
      }

      for (let index = 0; index < this.table_data.length; index++) {
        const element = this.table_data[index];
        var bHavEl1 = false;
        for (let index1 = 0; index1 < this.currentTableData.length; index1++) {
          const element1 = this.currentTableData[index1];

          if (this.unique_idEqual(element, element1)) {
            bHavEl1 = true;

            var nv1 = JSON.parse(JSON.stringify(element));
            var nv2 = JSON.parse(JSON.stringify(element1));
            delete nv2.isEdit;
            delete nv1.isEdit;
            if (JSON.stringify(nv1) != JSON.stringify(nv2)) {
              //

              console.log("(2 )", JSON.stringify(nv1));
              callBackChangedFunc(nv1, 2);
              break;
            }
          }
        }
        if (!bHavEl1) {
          console.log("(1 )", JSON.stringify(element));

          callBackChangedFunc(element, 1);
        }
      }
      
        //  
        for (let index = 0; index < this.currentTableData.length; index++) {
          const element = this.currentTableData[index];
          var bHavEl = false;
          for (let index1 = 0; index1 < this.table_data.length; index1++) {
            const element1 = this.table_data[index1];
            if (this.unique_idEqual(element, element1)) {
              bHavEl = true;

              break;
            }
          }

          if (!bHavEl) {
            // 
            console.log("(3 )", element);
            callBackChangedFunc(element, 3);
          }
        }
      

      this.currentTableData = JSON.parse(JSON.stringify(this.table_data));
      this.$emit(this.msgId, 0);
    },
    /* cancelAction() {
      //this.table_disable_edit = true;

      //this.$emit('cancelAction',JSON.stringify(this.currentTableData));
      //this.table_data = JSON.parse(JSON.stringify(this.currentTableData));
    },*/
    editAction() {
      this.table_disable_edit = false;
    },
    addAction(obj) {
      this.table_data.push(JSON.parse(JSON.stringify(obj)));
    },
    deleteAction(obj) {
      if (obj) {
        this.table_data.forEach((element, index) => {
          if (JSON.stringify(element) == JSON.stringify(obj)) {
            this.table_data.splice(index, 1);
            return;
          }
        });
      } else {
        if (this.currentRow == undefined) {
          console.log("");
          return;
        }

        this.table_data.forEach((element, index) => {
          if (this.unique_idEqual(element, this.currentRow)) {
            this.table_data.splice(index, 1);
            return;
          }
        });
      }
    },
    dataIsEqual(newCurr, newJs) {
      var nwe, cur;
      var newJs22 = newJs == undefined ? [] : JSON.parse(JSON.stringify(newJs));
      var newCurr22 =
        newCurr == undefined ? [] : JSON.parse(JSON.stringify(newCurr));

      if (newJs != undefined) {
        newJs22.forEach((element) => {
          delete element.isEdit;
        });
      }
      if (newCurr != undefined) {
        newCurr22.forEach((element) => {
          delete element.isEdit;
        });
      }

      nwe = JSON.stringify(newJs22);
      cur = JSON.stringify(newCurr22);

      return nwe === cur;
    },
    handleCurrentChange(val) {
      console.log(" ", JSON.stringify(val));
      this.currentRow = val;

      this.$emit(this.clickRowID, val);

      //
      if (this.table_disable_edit) {
        console.log(" ");
        this.table_data.forEach((item) => {
          item.isEdit = false;
        });
        return;
      }
      if (val) {
        this.table_data.forEach((item) => {
          if (this.unique_idEqual(item, val)) {
            item.isEdit = true;
          } else {
            item.isEdit = false;
          }
        });
      }
    },
    exportDataToExcel(outExcelName) {
      console.log(this.table_id);
      let tables = document.getElementById(this.table_id);
      let table_book = XLSX.utils.table_to_book(tables);
      var wr = XLSX.write(table_book, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
          new Blob([wr], { type: "application/octet-stream" }),
          outExcelName
        );
      } catch (e) {
        console.log(":", e, wr);
      }
    },
    printExcel() {
      const printContent = document.getElementById(this.table_id);
      // dom  
      const width = printContent.clientWidth;
      const height = printContent.clientHeight;
      // canvas
      const canvas = document.createElement("canvas");

      const scale = 4; // 
      canvas.width = width * scale; // canvas  * 
      canvas.height = height * scale; // canvas *
      canvas.style.width = width * scale + "px";
      canvas.style.height = height * scale + "px";
      canvas.getContext("2d").scale(scale, scale); // context,scale

      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; // 
      const scrollLeft =
        document.documentElement.scrollLeft || document.body.scrollLeft; // 

      html2canvas(printContent, {
        canvas,
        backgroundColor: null,
        useCORS: true,
        windowHeight: document.body.scrollHeight,
        scrollX: -scrollLeft, // 
        scrollY: -scrollTop,
      })
        .then((canvas) => {
          const url = canvas.toDataURL("image/png");

          printJS({
            printable: url,
            type: "image",
            documentTitle: "", // 
            style: "@page{size:auto;margin: 0cm 1cm 0cm 1cm;}", // 
          });
        })
        .catch((err) => {
          console.error(err);
        });
    },
    dbSelected(row, column, event) {
      this.$emit(this.dbClickRowID, row, column, event);
    },
  },
};
</script>

<style scoped lang="scss">
.inputNoBorder {
  widows: 100%;
  height: 100%;
}

.inputNoBorder >>> .el-input__inner {
  -webkit-appearance: none;
  background-color: transparent;
  background-image: none;
  border-radius: 4px;
  padding: 0px;
  width: 100%;
  text-align: center;
}
.inputNoBorder >>> .el-textarea__inner {
  border: 0;
  resize: none;
}

// 
@media print {
  @page {
    size: auto;
    margin: 3mm;
  }
  body {
    height: auto; //
  }
  .printPage {
    height: 291mm; //  
    position: relative;
    &::before {
      position: absolute;
      content: "HXY_";
      top: 50%;
      right: 50%;
      font-size: 48px;
      opacity: 0.1;
      transform: rotate(30deg);
    }
    &::after {
      position: absolute;
      content: "HXY_";
      bottom: 50%;
      left: 50%;
      font-size: 48px;
      opacity: 0.1;
      transform: rotate(30deg);
    }
  }
}
html,
body,
div {
  height: auto !important;
  font-size: 14px;
}
</style>