import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import httpApi from './api/index'
import store from './store'
import './icons' // icon
import Astrict from '@/utils/checkTimeout'
Vue.use(Astrict)

import edit_table from '@/views/editTable'// svg component

// register globally
Vue.component('edit-table', edit_table)
Vue.use(ElementUI);
Vue.config.productionTip = false
Vue.prototype.$httpApi = httpApi
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
