import Vue from 'vue'
import Vuex from 'vuex'
import router  from '../router';
import {resetRouter}  from '../router';

Vue.use(Vuex)

var meuArr = [

  {
        
    path:'/homePage',
    component: resolve => {
      require.ensure([], (require) => {
          resolve(require('../views/menu/HomePage.vue'))
      })},
    name:'homePage',
    meta:{
      title:'',
      icon:'el-icon-s-home'
    }
  },

        {
          path:'/Dash',
          component:() => import('@/views/menu/Dash.vue'),
          name:'Dash',
          meta:{
            title:'',
            icon:'el-icon-setting',
            children:[
              {
                path:'/zoneType',
                component:() => import('@/views/menu/params/zoneType.vue'),
                name:'zoneType',
                meta:{
                  title:'',
                  icon:'el-icon-picture-outline'
                }
              },
              {
                path:'/useBM',
                component:() => import('@/views/menu/params/useBM.vue'),
                name:'useBM',
                meta:{
                  title:'',
                  icon:'el-icon-s-management'
                }
              },
              {
               
                path:'/atsGroup',
                component:() => import('@/views/menu/params/atsGroup.vue'),
                name:'atsGroup',
                meta:{
                  title:'ATS',
                  icon:'el-icon-truck'
                }
              },
              {
              
                path:'/video',
                component:() => import('@/views/menu/params/video.vue'),
                name:'video',
                meta:{
                  title:'',
                  icon:'el-icon-video-camera'
                }
              },
              {
               
                path:'/atsMap',
                component:() => import('@/views/menu/params/atsMap.vue'),
                name:'atsMap',
                meta:{
                  title:'ATS',
                  icon:'el-icon-coordinate'
                }
              }

            ]
          }
        },
        
        
        {
        
          path:'/log',
          component:() => import('@/views/menu/Log.vue'),
          name:'Log',
          meta:{
            title:'',
            icon:'el-icon-document'
          }
        },
        
        {
          
          path:'/Line',
          component:() => import('@/views/menu/LineCfg.vue'),
          name:'Line',
          meta:{
            title:'',
            icon:'el-icon-edit'
          }
        },
        {
          id:12,
          path:'/User',
          component:() => import('@/views/menu/User.vue'),
          name:'User',
          meta:{
            title:'',
            icon:'el-icon-user',
           
          },
          
          enabel:true
        },
     
  
      ];

let levelType = {
  params:1,
  user:2,
  log:4,
  lineCfg:8
};

//menu
export var showMenuArr=[];

export default new Vuex.Store({
    state:{
        routes:[],
        adminleftnavnum:"/homePage"
    },
    mutations:{
      resetRoutes(state){
        state.routes = [];
        showMenuArr = [];
      },
      initRoutes(state,nlevel){

        if (state.routes.length > 0) {
          return;
        }
        resetRouter(); 
       // console.log('', router.getRoutes());   

        state.routes = [];
        showMenuArr = [];  
        meuArr.forEach(item => {
          
          //
          if( (nlevel & levelType.params) == 0 &&  item.name == 'Dash')
          {
                return;
          }
          else if( (nlevel & levelType.user) == 0 &&  item.name == 'User')
          {
                return;
          }
          else if( (nlevel & levelType.log) == 0 &&  item.name == 'Log')
          {
                return;
          }
          else if( (nlevel & levelType.lineCfg) == 0 &&  item.name == 'Line')
          {
                return;
          }

          showMenuArr.push(item);

          if(item.meta.children && item.meta.children.length > 0)
          {

            item.meta.children.forEach(subItem=>{
              router.addRoute('home',subItem);
            })

          }else
          {
            router.addRoute('home',item);
          }
          
        });
          
        //console.log('', router.getRoutes())
        router.addRoute({ path: '*', redirect: '/404', meta:{hidden: true }});
       
  

        state.routes = showMenuArr;
       // alert(JSON.stringify(router.getRoutes()))
        window.sessionStorage.setItem('nlevel',nlevel);
      }
        
    }


})