<template>

  <div>
    <div class="background">
     
    </div>

    <div class="front">
      <el-card class="card">
        <div
          slot="header"
          class="title"
        >
          <span>{{form.name}}</span>
        </div>
        <div>
          <el-form
            ref="form"
            :model="form"
            label-width="100px"
            class="form-all1"
          >
            <el-form-item
              label=""
              prop="password"
              class="form-item"
            >
              <el-input
                type="password"
                v-model="form.password"
                placeholder=""
                @keyup.enter.native="loginAction('form')"
                show-password
                class="form-input"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              label-width="0"
              style="text-align:center"
            >
              <el-button
                type="primary"
                @click="unLock"
                :loading="loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
              ></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { Message } from "element-ui";
export default {
  data() {
    return {
      form: {
        name: "admin",
        password: "",
        ip: "",
        dbIp: "",
      },
      loading: false,
      
    };
  },
  methods: {
    unLock() {
      const that = this;
      that.loading = true;
      this.$httpApi
        .login_post(this.form)
        .then(function (response) {
          that.loading = false;
          if (response.data.code == "0") {
            window.sessionStorage.setItem("token", response.data.Authorization);

           
            console.log("");
            that.$router.back();
          } else {
            Message.error(response.data.message);
          }
        })
        .catch(function () {
          that.loading = false;
        });
    },
  },
  mounted() {
    if (localStorage.ip) {
      this.form.ip = localStorage.ip;
    }

    if (localStorage.dbIp) {
      this.form.dbIp = localStorage.dbIp;
    }
    this.form.name = window.sessionStorage.getItem("name");
  },
};
</script>

<style scoped>
.background {
  height: 100%;
  width: 100%;
  z-index: -1;
  padding: 0;
  margin: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-image: url("../assets/lock.gif");
}
.front {
  z-index: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}
.card {
  width: 400px;

  border-color: rgba(200, 200, 200, 0.5);
  border-radius: 18px;
  z-index: 2;
}
.title {
  text-align: center;
  font-size: 24px;
}

.form-item {
  margin-right: 10px;
}
</style>
